//vuex   
import { createStore } from "vuex";

export default createStore({
    //   
    state: {
        EditGroups : false,
        GroupPreview : false,

    }
    //   state  
    ,getters:{
        
    } 
    //  ()
    ,mutations:{
        OnEdit(state){
            state.EditGroups = true;
        },
        OffEdit(state){
            state.EditGroups = false;
        },
        OnGroupPreview(state,corrent){
            state.GroupPreview = corrent;
        },
        OffGroupPreview(state){
            state.GroupPreview = false;
        }
    },
    //  Mutations  
    actions:{

    },
    // 
    modules:{

    }
});


